import { useState } from 'react';
import { Navbar, Container, NavDropdown, Nav, Button  } from 'react-bootstrap';
import './App.css';
import data from './data';
import { Link, Route, Switch } from 'react-router-dom';
import Detail from './Detail';
import styled from 'styled-components';
import axios from 'axios';

function App() {
  let [shoes, shoesChage] = useState(data);
  let 박스 = styled.div`
  padding : 20px;
  `;
  let 제목 = styled.h4`
  font-size : 25px;
  color : ${ (props) => { return props.색상; } };
  `;
  return (
    <div className="App">
      <Navbar bg="light" expand="lg">
  <Container>
    <Navbar.Brand href="/">ShoeShop</Navbar.Brand>
    <Navbar.Toggle aria-controls="basic-navbar-nav" />
    <Navbar.Collapse id="basic-navbar-nav">
      <Nav className="me-auto">
        <Nav.Link as={Link} to="/">Home</Nav.Link>
        <Nav.Link as={Link} to="/detail/0">Detail</Nav.Link>
        <NavDropdown title="Dropdown" id="basic-nav-dropdown">
          <NavDropdown.Item href="#action/3.1">Action</NavDropdown.Item>
          <NavDropdown.Item href="#action/3.2">Another action</NavDropdown.Item>
          <NavDropdown.Item href="#action/3.3">Something</NavDropdown.Item>
          <NavDropdown.Divider />
          <NavDropdown.Item href="#action/3.4">Separated link</NavDropdown.Item>
        </NavDropdown>
      </Nav>
    </Navbar.Collapse>
  </Container>
</Navbar>

<Switch>
<Route path="/detail/:id">
        <Detail shoes={ shoes }></Detail>
      </Route>
  
    <Route path="/">
    <div className="background">
      <h2>20% Sale</h2>
      <p>Lorem ipsum dolor sit amet consectetur, adipisicing elit. Itaque assumenda atque aspernatur sapiente saepe! Repellat, voluptas. Unde, laborum. Dicta assumenda aut corrupti sit exercitationem aliquam recusandae dolore incidunt quae natus!</p>
    </div>
    
    
    <div className="container">
      <div className="row">
    
        {
          shoes.map(function(item, num){
            return (
              <ProductCard item={ item } num={ num } ></ProductCard>
            );
          })
        }
      </div>
    </div>

    <Button variant="primary" onClick={ () => {
      axios.get('https://codingapple1.github.io/shop/data2.json')
      .then( (result) => {
        // console.log(result.data);
        // let temp = result.data;
        // let newArr =[...shoes];
        // result.data.map((a)=>{
        //   newArr.push(a);
        // })
        shoesChage([...shoes, ...result.data]);
       })
      .catch( () => { console.log('실패했어용'); } )
    } }>더보기</Button>

    </Route>
      

</Switch>


    </div>
  );
}

function ProductCard(props){
  return (
    <div className="col-md-4" num={ props.num }>
      <Link to={ `/detail/${props.num}` }>
        <img src={ `https://codingapple1.github.io/shop/shoes${ props.num + 1 }.jpg` } width="100%"></img>
        <h4> { props.item.title } </h4>
        <p> { props.item.price } &amp; { props.item.content } </p>
      </Link>
    </div>
  );
}


export default App;
