import { useState, useEffect } from 'react';
import { useHistory, useParams } from 'react-router-dom';
import './temp.scss';

function Detail(props){
    let history = useHistory();
    let { id } = useParams();
    let productInfo = props.shoes.find( (a) => { return a.id == id } );
    let [alert, alertChange] = useState(true);
    useEffect(()=>{
      let timer = setTimeout(()=> { alertChange(false) } , 2000);
      return () => { clearTimeout(timer); };
    }, [alert]);
    return(
        <div className="container">
      <div className="row">
        <div className="col-12">
        {
          alert == true
          ? <div className="my-alert my-3"> <p>재고가 얼마 남지 않았습니다</p> </div>
          : null
        }
        </div>
        
        <div className="col-md-6">
          <img src={ `https://codingapple1.github.io/shop/shoes${Number(productInfo.id) + 1}.jpg` } width="100%" />
        </div>
        <div className="col-md-6 mt-4">
          <h4 className="pt-5 red"> { productInfo.title } </h4>
          <p> { productInfo.content } </p>
          <p> { productInfo.price } </p>
          <button className="btn btn-danger">주문하기</button> 
          <button className="btn btn-danger" onClick={ ()=> { history.goBack(); } }>뒤로가기</button> 
        </div>
      </div>
</div>
    );
}

export default Detail;